<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="index.html">
                        Каталог
                    </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link" href="cart.html">
                        Корзина
                    </a>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        Оформление заказа
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                Заказ оформлен <span v-if="orderInfo != null">№ {{ orderInfo.id }}</span>
            </h1>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST" v-if="orderInfo != null">
                <div class="cart__field">
                    <p class="cart__message">
                        Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
                        Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
                    </p>

                    <ul class="dictionary">
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Получатель
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.name }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Адрес доставки
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.address }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Телефон
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.phone }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Email
                            </span>
                            <span class="dictionary__value">
                                {{ orderInfo.email }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Способ оплаты
                            </span>
                            <span class="dictionary__value">
                                картой при получении
                            </span>
                        </li>
                    </ul>
                </div>

                <CartOrders :products="products" :total-price="totalPrice" :total-amount="totalAmount"/>
            </form>
            <div v-else>
                <img src="../assets/img/gif/1494.gif" alt="">
            </div>
        </section>
    </main>
</template>

<script>
import CartOrders from '@/components/CartOrders.vue';
import { mapGetters } from 'vuex';
export default {
    components: {
        CartOrders
    },
    computed: {
        ...mapGetters({orderInfo: 'orderInfo'}),
        products() {
            if(this.orderInfo == null) {
                return []
            }
            return this.orderInfo.basket.items
        },
        totalPrice() {
            if(this.orderInfo == null) {
                return 0
            }
            return this.orderInfo.totalPrice
        },
        totalAmount() {
            if(this.orderInfo == null) {
                return 0
            }
            let amount = 0
            this.orderInfo.basket.items.forEach(item => {
                amount += item.amount
            })
            return amount
        }
    },
    created() {
        if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
            return
        }
        this.$store.dispatch('loadOrderInfo', this.$route.params.id)
    }
}
</script>