import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { ref, reactive } from 'vue';
import { useStore } from 'vuex';

export default function () {
    const $store = useStore();

    const productData = ref(null);
    const fetchStatus = reactive({
        isLoading: true,
        isFailed: false
    })

    const productAmount = ref(1);
    const productAdded = ref(false);
    const productAddSending = ref(false);
    const isShowAddedMassage = ref(false);
    const doAddToCart = () => {
      productAdded.value = false,
        productAddSending.value = true

      $store.dispatch('addProductToCart', {
        productId: productData.value.id,
        amount: productAmount.value
      })
        .then(() => {
          productAdded.value = true,
            productAddSending.value = false,
            isShowAddedMassage.value = true
        })
    };

    const fetchProduct = (productId) => {
        fetchStatus.isLoading = true;
        fetchStatus.isFailed = false;
        axios.get(API_BASE_URL + `/api/products/` + productId)
            .then(response => {
                // const product = response.data;
                
                productData.value = Object.assign(response.data, {
                    pricePretty: numberFormat(response.data.price),
                    imageUrl: response.data.image.file.url
                })
                console.log(Object.assign(response.data, {
                    pricePretty: numberFormat(response.data.price),
                    imageUrl: response.data.image.file.url
                }));
            })
            .catch(() => fetchStatus.isFailed = true)
            .finally(() => fetchStatus.isLoading = false)
    };

    return {
        product: productData,
        status: fetchStatus,
        fetchProduct,
        doAddToCart,
        productAmount,
        productAdded,
        productAddSending,
        isShowAddedMassage
    }
}