<template>
    <span class="content__info">
        {{ totalAmount }} {{ textCorrectCase }}
    </span>
</template>

<script>
import { mapGetters } from 'vuex';
import amountText from '@/helpers/amountText';
export default {
    computed: {
        ...mapGetters({
            totalAmount: "totalProductAmount",
        }),
        textCorrectCase() {
            return amountText (this.totalAmount || 0)
        },
    },
}
</script>