export default function amountText(totalAmount) {
  if (!(typeof totalAmount == 'number')) {
    totalAmount = 0
  }
  const text = 'товар'
  let suffix = ''
  const numberArr = totalAmount.toString().split('', -1)
  const lastNumber = Number(numberArr[numberArr.length - 1])

  if (1 < lastNumber && lastNumber < 5) {
    suffix = 'a'
  }
  if (lastNumber > 4 || (totalAmount > 9 && totalAmount < 15) || lastNumber == 0) {
    suffix = 'ов'
  }
  return text + suffix

}