<template>
    <BaseFormField :title="title" :error="error">
        <input class="form__input" :type="type" v-model="localFormDataValue" :placeholder="placeholder">
    </BaseFormField>
</template>
<script>
import formFieldMixin from '@/mixins/FormFieldMixin'
export default {
    props: {
        type: {
            default: 'text'
        },
        formDataValue: {
            default: ''
        },

    },
    mixins: [formFieldMixin],

    data() {
        return {
            localFormDataValue: this.formDataValue,
        };
    },

    watch: {
        formDataValue(value) {
            this.localFormDataValue = value;
        },
        localFormDataValue(value) {
            this.$emit('update:modelValue', value);
        }
    },
}
</script>